.top {
  margin-top: 100px;
  height: 110vh;
  background-image: url("topbackground.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.image {
  height: 100%;
  width: 100%;
  position: relative;
  background-color: rgb(0, 0, 0, 0.4);
}

.topbackground-text {
  margin-left: 30px;
  margin-top: 30px;
  width: 50vw;
  height: 80%;
  font-size: 60px;
  color: White;
  float: left;
}
.background-merge {
  position: absolute;
  bottom: 0px;
  height: 40%;
  width: 100%;
  background-image: linear-gradient(
    to bottom,
    rgb(0, 0, 0, 0),
    rgb(0, 0, 0, 1)
  );
}
.topbackground-text .learnmore {
  font-size: 20px;
  text-decoration-style: solid;
  float: left;
  text-decoration: underline;
  cursor: pointer;
  padding: 20px;
}
