.Navigation {
  vertical-align: middle;
  background-color: rgb(0, 0, 0);
  display: inline-block;
  position: fixed;
  top: 0;
  padding: 20px;
  padding-right: 30px;
  padding-top: 0px;
  padding-bottom: 30px;
  height: 70px;
  margin: auto;
  width: 98%;
  box-shadow: 0px 1px 7px 0px black;
  overflow: hidden;
  z-index: 1;
}

.nav-links {
  font-weight: bolder;
  font-size: larger;
  color: white;
  float: right;
  padding-top: 20px;
  padding-bottom: 20px;
  margin: 20px;
  cursor: pointer;
}

.nav-links:hover {
  border: 3px;
  border-bottom-style: solid;
  border-color: rgb(255, 115, 0);
  color: rgb(255, 115, 0);
}

.logo {
  margin-top: 10px;
  margin-left: 40px;
  height: 80px;
  width: 100px;
  float: left;
  margin-right: 10px;
}
